.nav {
	list-style-type:none;
	margin:0;
	padding:0;
}

.navItem {
	@extend .u-inlineBlock;
	display:none;
}

.navItem.isShownOnMobile {
	@extend .u-inlineBlock;
}

.navItem-icon {
	vertical-align: middle;
	position: relative;
	top: -2px;
	margin: 0 2px 0 0;
	transition:color .35s;
}

.navItem-iconLocations {
	font-size: 18px;
	top:-1px;
	position:relative;
}

.navItem-iconDonate {
	font-size: 19px;
}

.navItem-link {
	color:black;
	text-decoration: none;
	font-size: 15px;
	line-height:20px;
	transition:color .35s;
}

.navItem-link:hover {
	color:white;
}

.navItem-link:hover .navItem-icon {
	color:$grey;
}

.navItem-btn {
	border-color:black;
	transition:all .35s;
	margin:0 0 0 .5em;
	font-size: 15px;
	cursor: pointer;
}

.navItem-btn:hover {
	color:black;
}

.nav.hasScrolled {
	background:white;
}

@media (min-width:$screen-md-min) {
	.navItem {
		margin:0 10px 0 0;
		display:inline-block;
	}
	.navItem-link {
		color:white;
		font-weight:600;
		padding:5px 15px;
		border:2px solid transparent;
		border-radius:5px;
	}
	.navItem-link:hover .navItem-icon {
		color:$brand;
	}
	.navItem-btn {
		border-color:white;
	}
	.navItem-btn:hover {
		color:white;
		border-color:$brand;
	}
	.navItem-iconLocations {
		font-size: 22px;
	}
	.navItem-iconDonate,
	.navItem-link {
		letter-spacing: 1px;
	}
	.globalHeader.isSticky .navItem-link:hover {
		color:$brand;
	}

	.globalHeader.isSticky .navItem-btn:hover {
		color:black;
	}
}