.greyBoxes img {
	vertical-align: bottom;
}

.greyBoxes-title {
	vertical-align: middle;
	font-size: 18px;
	text-transform: none;
}

.greyBoxes-title svg,
.greyBoxes-title img,
.greyBoxes-title .icon {
	height: 30px;
	margin-right: 10px;
	min-width:30px;
	display:inline-block;
	vertical-align: middle;
	background-size: contain;
}

.greyBoxes-box {
	@extend .u-greyLightBg;
    padding:30px 30px 10px;
	margin:0 0 12px 0;
	text-align:center;
}

.greyBoxes-box br {
	display:none;
}

.greyBoxes-gridItem {
	@extend .u-greyLightBg;
	max-width:49%;
	margin:1px;
	padding:30px 30px 10px;
	text-align: left;
}

@media (min-width:$screen-md-min) {
	.greyBoxes {
		align-items:stretch;
		justify-content: center;
		margin: 0;
	}
	.greyBoxes-box br {
		display:block;
	}
	.greyBoxes-box {
		justify-content:center;
		text-align: left;
		margin:0 12px 12px 0;
		flex: 1 1 50%;
	}
	.greyBoxes-box.u-textCenter {
		text-align: center;
	}
	.greyBoxes-box.isFullWidth {
		flex:1 1 100%;
	}
	.greyBoxes-title {
		font-size: 22px;
	}
	.greyBoxes-box ul {
		display: inline-block;
		vertical-align: top;
		margin-top: 0;
		width: 48%;
	}
	.greyBoxes-box.u-textCenter ul {
		text-align: left;
	}
}