.teamMember-container {
	display: flex;
}

.teamMember {
	width: 50%;
	flex: 1 1 50%;
}

.teamMember {
	position: relative;
}

.teamMember-image {
	object-fit:cover;
	width:100%;
	height: 100%;
	vertical-align: bottom;
}

.teamMember-caption {
	color: white;
	position:absolute;
	left: 0;
	right: 0;
	bottom: 0;
	padding:25px 15px;
	width: 100%;
	text-align: center;
}

.teamMember-caption p {
	margin: 0;
	line-height: 1.2;
	@extend .u-textMd;
}

@media (min-width:$screen-sm-min) {
	.teamMember-container {
		align-items:stretch;
		justify-content:center;
	}

	.teamMember {
		flex: 1;
	}
}