.getHelp {
	@extend .container-fluid;
	transition:max-height .35s;
	max-height:0;
	overflow:hidden;
	display: none;
}

.getHelp.isExpanded {
	max-height:999px;
}

.getHelp-zip {
	position: relative;
	margin:0 5px 0 0;
	min-width:35%;
}

.getHelp-zip input {
	padding-right:25px;
}

.getHelp-geolocate {
	position: absolute;
	right:10px;
	top:12px;
	cursor: pointer;
}

.getHelp-title {

}

.getHelp-steps {
	margin:0 0 0 10px;
	position: relative;
}

.getHelp-steps .slick-slide {
	height: auto;
}

.getHelp-steps:before {
	content: "";
	height: 150px;
	top: 0;
	left:-10px;
	width: 2px;
	background: $greyLighter;
	position: absolute;
	margin: 0 35px 0 0;
}

.getHelp-form {
	width: 100%;
	background: white;
	z-index:10;
	overflow: hidden;
}

.getHelp-form:focus {
	outline:none;
}

.getHelp-form:first-child {
	z-index:20;
}

.getHelp-arrow {
	font-size: 36px;
	cursor: pointer;
	color:$grey;
	float: left;
}

.getHelp-categories {
	@extend .u-resetList;
	column-count:2;
	column-gutter:10px;
	//font-size: 14px;
	margin:0 0 0 25px;
}

@media (min-width:$screen-md-min) {
	.getHelp {
		display: block;
	}
}