.globalFooter {
	font-size:14px;
	line-height:1.3;
	background:$blueDark;
	color:white;
	padding:3em 0 1em;
	position:relative;
	z-index:100;
}

.globalFooter-wrapper {
	@extend .container;
}

.globalFooter-section {
	@extend .row;
}

.globalFooter:before {
	content:"";
	display:block;
	background:url(../images/global/shield-noLine.svg) no-repeat center center;
	background-size:contain;
	width:64px;
	height:76px;
	position:absolute;
	top:-38px;
	left:50%;
	margin:0 0 0 -33px;
}

.globalFooter-copyright {
	color:#5a5d64;
	font-size:13px;
	line-height: 1.4;
}

.globalFooter-copyrightLink {
	color:#5a5d64;
	@extend .u-linkReset;
}

.globalFooter-copyrightLink:hover {
	color:$greyLight;
}

.globalFooter-search {
	display: flex;
	position:relative;
	overflow:hidden;
}

input:not([type=submit]):not([type=radio]):not([type=checkbox]).globalFooter-input {
	flex-grow:1;
	margin:0 5px 0 0;
	color:white;
	border:none;
	border:1px solid #41454e;
	background:#41454e;
	padding:10px;
}

input[type="submit"].globalFooter-submit {
	background:rgb(105,109,120);
	color:white;
	margin: 0;
	padding: 2px 8px 3px;
	font-size: 16px;
}

input[type="submit"].globalFooter-submit:hover {
	background:$greyLight;
}

.globalFooter-nav {
	@extend .nav;
	column-count:2;
	margin:0 0 1em 0;
}

.globalFooter-navLink {
	@extend .globalFooter-copyrightLink;
	display:block;
	padding:3px 0;
	font-size:14px;
	color:white;
}

.globalFooter-navLink:hover {
	color:$greyLight;
}

.globalFooter hr {
	border:none;
	border-top:1px solid $grey;
	margin:2em 0;
}

@media (min-width:$screen-sm-min) {
	.globalFooter-nav { 
		column-count:5; 
		margin:0; 
	}
	.globalFooter-search {
		margin-top: 1em;
	}
}