.heroContainer {
	display:block;
	position:relative;
	overflow:hidden;
	width:100%;
	padding:45px 0 0;
	margin:0 0 2em 0;
}

.heroArrow {
	display:none;
}

.hero {
	width:100%;
	position:relative;
	display:block;
	overflow:hidden;
	height:50vh;
}

.heroImage-container,
.heroText-container {
	height:100%;
	overflow:hidden;
}

.heroText-container {
	position:absolute;
	top:0;
	left:0;
	width:100%;
	height: 100%;
	color: white;
}

.heroText {
	top:50%;
	transform:translate(0,-50%);
	position:relative;
	transition:all .15s;
	padding:0 2em;
}

.hero .fluid-width-video-wrapper {
	height: 100%;
	z-index:30;
	position:absolute;
	left:0;
	right:0;
	top:0;
	bottom:0;
}

.heroImage {
	object-fit:cover;
	object-position:center top;
	height:100%;
	width:100%;
}

.heroText-headline {
	@extend .serif;
	font-size:16px;
	line-height:1.2;
	margin:0;
}

.heroText-subheadline {
	margin:.5em 0 1em;
	font-size:12px;
}

/* Slick Slider Overrides */

.slick-dots { bottom:-30px;  margin:0; }
.slick-list, .slick-track { height:100%; width:100%; }
.slick-loading .slick-list { background:none; }
.slick-next, .slick-prev { transition:all .35s; z-index:2000; left:25px; width:50px; height:50px; text-align: center; border-radius:50%; opacity:0; }
.slick-dots li button:before, .slick-dots li button:after { font-size:12px; }

@media (min-width:$screen-sm-min) {

	.slick-slider:hover .slick-next, .slick-slider:hover .slick-prev { opacity:1; }
	.slick-next:hover, .slick-prev:hover { color:$brand; }
	.slick-next { right: 25px; left: auto; }
	.slick-next:before, .slick-prev:before { font: normal normal normal 20px/1 FontAwesome; transition:color .35s;}
	.slick-prev:before { content: "\f053"; }
	.slick-next:before { content:"\f054"; }
	.slick-next:hover:before, .slick-prev:hover:before { color:$brand; }
	.slick-dots { bottom:7em; margin:0; }
	.slick-dots li button:before, .slick-dots li button:after { color:rgba(255,255,255,.9); }
	.slick-dots li.slick-active button::before { color:white; }
	.heroImage {
		z-index:10;
	}

	.heroSlider {
		height: 100%;
	}

	.heroArrow { 
		bottom:6%;
		display:block;
	}

	.heroContainer { 
		height:800px; 
		height: 100vh; 
		padding:0;
		margin:0;
	}

	.heroText-container {
		color:white;
		top:50%;
		transform:translate(0,-50%);
		position: relative;
		z-index:40;
	}

	.hero {
		height:550px;
	}

	.heroText-container,
	.heroImage-container {
		height:auto;
		overflow:visible;
	}

	.heroText-container {
		height:100%;
	}

	// Bring the current slide's hero text to the forefront
	.slick-active .heroText {
		z-index:1000;
	}

	.heroImage {
		position:absolute;
		left:0;
		top:0;
		right:0;
		bottom:0;
		width:100%;
		transform:translate3d(0,0,0);
	}

	.hero {
		height:100%;
		background-size:cover;
		background-repeat:no-repeat;
		background-position:center top;
		background-attachment:fixed;
		transform:translate3d(0,0,0);
	}

	.hero:before {
		content: '';
		position: absolute;
		z-index:20;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
		background-image: linear-gradient(rgba(0,0,0,.9),rgba(0,0,0,.5));
		opacity: .8; 
	}

	.heroArrow {
		border:2px solid white;
		border-radius:50%;
		width:50px;
		height:50px;
		line-height: 2;
		font-size:25px;
		color:white;
		text-align: center;
		position: absolute;
		z-index:30;
		margin:0 0 0 -25px;
		left:50%;
		bottom:4%;
		cursor: pointer;
		transition: background .35s;
		&:hover {
			background:white;
			color:$brand;
		}
	}

	.heroText-headline { 
		font-size:33px;
		line-height:1.3;
	}

	.heroText-subheadline {
		font-size: 15px;
	}

}

@media (min-width:$screen-lg-min) {
	.heroText-container {
		padding:0 18.5em;
	}
}
