.ministriesGrid {
	@extend .u-resetList;
	font-size:0;
	text-align: center;
	display:flex;
	flex-direction: row;
	flex-wrap: wrap;
    justify-content: center;
    align-content: center;
    align-items: center;
}

.ministriesGrid-item {
	@extend .u-inlineBlock;
	font-size:16px;
	flex: 1 1 50%;
	position:relative;
	background: black;
}

.ministriesGrid-image {
	object-fit:cover;
	opacity:0;
	float:left;
	transition:all .5s;
}

.ministriesGrid-image.lazyloaded {
	width:100%;
	height:100%;
	max-height: 95px;
	opacity:.8;
}

.ministriesGrid-link {
	text-decoration: none;
	display: block;
	color: black;
}

.ministriesGrid-title {
	color:white;
	position: absolute;
	bottom:1em;
	width:100%;
	opacity:1;
	transition:opacity .35s;
	font-weight: 600;
	text-shadow: 0 0 3px rgba(0,0,0,.25);
	padding: 0 10px;
	line-height: 1.2;
	@extend .u-textCenter;
}

.no-touch .ministriesGrid-item:hover .ministriesGrid-title {
	opacity:0;
}

.ministriesGrid-overlay {
	background:rgba(207,227,232,.95);
	width:100%;
	height:100%;
	position: relative;
	z-index:100;
	position:absolute;
	left:0;
	top:0;
	bottom:0;
	right:0;
}

.ministriesGrid-content {
	position:relative;
	top:50%;
	transform:translate(0,-50%);
	padding:0 10px;
}

.ministriesGrid-caption {
	font-size:13px;
	line-height: 1.2;
	margin:0;
}

.ministriesGrid-icon {
	@extend .icon;
	height: 15px;
	margin:10px auto;
}

.ministriesGrid-btn {
	max-width:100%;
	font-size:12px;
	padding:5px 10px;
}

@media (min-width:$screen-sm-min) {
	.ministriesGrid-image.lazyloaded {
		max-height: 238px;
	}
	.ministriesGrid-icon {
		height:20px;
	}
	.ministriesGrid-btn {
		font-size:14px;
	}
}

@media (min-width:$screen-md-min) {
	.ministriesGrid-image.lazyloaded {
		max-height: 290px;
	}
	.ministriesGrid-icon {
		height:30px;
	}
	.ministriesGrid-item {
		flex: 1 1 25%;
	}
	.ministriesGrid-container.isContained .ministriesGrid-item {
		flex: 1 1 20%;
		margin: 0 15px 15px 0;
	}
}
