.videoGrid {
	
}

.videoGrid-item {
	margin-bottom:25px;
	overflow:hidden;
}

.videoLink {
	display:table;
	color:white;
	text-decoration: none;
	width:100%;
	text-align: center;
	height:87px;
	position:relative;
}

.videoLink:hover {
	color:$greyLighter;
}

.videoLink:hover:after {
	opacity:1;
}

.videoTitle {
	display:table-cell;
	vertical-align: middle;
	position:relative;
	z-index:30;
	font-weight:600;
	background:rgba(0,0,0,.4);
}

.videoTitle-icon {
	display:block;
}

.videoThumbnail {
	position:absolute;
	left:0;
	top:0;
	text-align:center;
	z-index:10;
}

@media (min-width:$screen-sm-min) {
	.videoLink {
		height: 175px;
	}
}