.donateHeader {
	@extend .container-fluid;
	display: none;
}

.donateHeader-goods {
	@extend .u-flexBox;
}

.donateHeader-recurring {
	@extend .u-flexChild;
}

.donateHeader-submit {
	float: right;
	text-align:right;
	@extend .u-flexChild;
}

@media (min-width:$screen-md-min) {
	.donateHeader-zip {
		position: relative;
	}

	.donateHeader-geolocate {
		position: absolute;
		right: 7px;
		top: 12px;
		cursor: pointer;
	}

	input:not([type=submit]):not([type=radio]):not([type=checkbox]).donateHeader-zipInput {
		border-top-right-radius: 0;
		border-bottom-right-radius: 0;
		border-right:0;
		max-width: 150px;
		padding-right:25px;
		margin-right: -4px;
	}

	input[type="submit"].donateHeader-zipSubmit {
		border-top-left-radius: 0;
		border-bottom-left-radius: 0;
		padding-top: 11px;
	}

	.donateHeader-amounts {
		margin-bottom: 20px;
	}

	.donateHeader-amounts .btn {
		margin-right:25px;
		border-width:1px;
		font-weight: 700;
		font-size: 14px;
		padding:10px 25px;
		&:last-child {
			margin-right: 0;
		}
	}
	.donateHeader.isExpanded {
		max-height:999px;
		padding-bottom: 20px;
	}
	.donateHeader {
		transition:max-height .35s;
		max-height:0;
		overflow:hidden;
		display: block;
	}
	.donateHeader-recurring label, 
	.donateHeader-recurring input[type="checkbox"] {
		width: auto;
	}
	.donateHeader-recurring label {
		//font-size: 14px;
	}
	.donateHeader-submit .btn {
		padding:5px 50px;
		//font-size: 14px;
	}
	.donateHeader-monetary {
		padding-left: 2em;
		margin-left: 1em;
		border-left:2px solid $greyLighter;
	}
}