.section {
	padding-top:1em;
	padding-bottom: 1em;
}

.section-container {
	@extend .container;
}

.section-row {
	@extend .row;
}

.section-row img,
.section-row svg {
	margin:0 auto;
	display: block;
}

.section-title br,
.section-subtitle br {
	display:none;
}

.section-title,
.section-subtitle {
	@extend .u-textCenter;
	margin-right:10px;
	margin-left:10px;
}

@media (min-width:$screen-md-min) {
	.section { 
		padding:2em 0; 
	}
	.section-title {
		margin-bottom: 50px;
	}
	.section-title,
	.section-subtitle {
		margin-right:0px;
		margin-left:0px;
	}
	.section-title + .section-subtitle {
		margin:-50px 0 50px;
	}
	.section-title br,
	.section-subtitle br {
		display:block;
	}
	.section-row {
		display:flex;
		align-items:center;
	}
}