.seeWhatsHappening {
	@extend .u-greyLightBg;
	@extend .u-textCenter;
	@extend .u-flexBox;
	align-items: center;
	justify-content: center;
	padding: 3em 0;
}

.seeWhatsHappening.isFooter {
	display:block;
	background-color:transparent;
	background-size:cover;
	background-repeat:no-repeat;
	background-position:center right;
	@extend .u-fullHeight;
}

.seeWhatsHapenning-content {
	text-align: right;
}

.seeWhatsHappening.isFooter .seeWhatsHappening-content {
	max-width: 300px;
	margin: 0 auto;
}

.seeWhatsHappening-title {
	font-weight: 600;
	font-size: 22px;
	line-height: 1.2;
	margin: 0 0 25px 0;
}

.seeWhatsHappening.isFooter .seeWhatsHappening-title {
	//font-size: 24px;
	font-weight: 700;
}

.seeWhatsHappening-form {
	@extend .u-flexBox;
}

input:not([type=submit]):not([type=radio]):not([type=checkbox]).seeWhatsHappening-input {
	background-color:white;
	border-right:none;
	border-top-right-radius:0;
	border-bottom-right-radius:0;
	margin:0;
}

input.seeWhatsHappening-submit {
	max-width:999px;
	//border-top-left-radius: 0;
	//border-bottom-left-radius: 0;
}

@media (min-width:$screen-sm-min) {
	.seeWhatsHappening-title {
		font-size: 27px;
	}
	.seeWhatsHappening.isFooter {
		background-position:center top;
	}
	.seeWhatsHappening.isFooter .seeWhatsHappening-content {
		max-width: 400px;
		margin-top: 25vh;
	}
}