.flyoutContainer {
	background: rgba(127,131,140,.99);
	position:fixed;
	z-index:3000;
	top:0;
	right:0;
	width:50%;
	max-width:225px;
	padding:30px;
	height:100%;
	transition:transform .35s;
	transform:translate3d(100%,0,0);
}

.flyoutContainer.isVisible {
	transform:translate3d(0,0,0);
}

.flyoutNav {
	height:100%;
	width:100%;
	@extend .nav;
}

.flyoutNavItem-link {
	color:white;
	text-decoration:none;
	font-weight:300;
	display:block;
	padding:10px 0;
	border-bottom:1px solid #72767e;
	transition:all .35s;
}

.flyoutNavItem-link:hover {
	color:$greyDark;
}

.flyoutNavItem:last-child .flyoutNavItem-link {
	border-color:transparent;
}

.flyoutClose {
	color: white;
	position: absolute;
	right: 10px;
	top: 10px;
	width:25px;
	height:25px;
	font-size: 28px;
	cursor:pointer;
}

.flyoutClose:hover .flyoutClose-icon {
	fill:$greyDark;
}

.flyoutClose-icon {
	fill:white;
	max-width: 100%;
	height:auto;
	transition:fill .35s;
}

.no-svg .flyoutClose:after {
	content:"x";
}

@media (min-width:$screen-sm-min) {
	.flyoutContainer { width:25%; }
}