.globalHeader {
	@extend .container-fluid;
	position: fixed;
	width:100%;
	z-index:2000;
	padding-top: 5px;
	padding-bottom: 5px;
	transition:all .35s;
	transform:translate3d(0,0,0);
	background:white;
	font-size: 14px;
	line-height: 1.4;
}

.globalHeader input:not([type=submit]) {
	font-size: 17px;
}

.globalHeader:after {
	content: "";
	width: 103%;
	height: 2px;
	background: rgba(0,0,0,.05);
	left: 0;
	position: absolute;
	bottom: -2px;
}

.globalHeader-wrapper {
	@extend .row-fluid;
	display:flex;
	align-items: center;
}
.globalHeader-logoContainer,
.globalHeader-navContainer {
	flex-grow:1;
}

.globalHeader-logoContainer {
	margin:0;
	text-decoration:none;
}

.globalHeader-logo {
	height: 30px;
	width: 48px;
	opacity:1;
	transition:all .35s;
	top:3px;
    position: relative;
}

.globalHeader-logoContainer:hover .globalHeader-logo {
	filter:drop-shadow(0 0 0 10px #888);
	//transform:translate(-1px,-1px);
}

.globalHeader-tagline {
	@extend .serif;
	display:none;
}

.globalHeader-logoContainer:hover .globalHeader-tagline {
	color:$brand;
}

.globalHeader-navContainer,
.globalHeader-logoContainer,
.globalHeader-nav,
.globalHeader-menuTrigger {
	@extend .u-inlineBlock;
}

.globalHeader-navContainer {
	transition:all .35s;
	text-align: right;
	width:100%;
}

.globalHeader-menuTrigger {
	color:black;
	padding:0 0 0 2em;
	transition:color .35s;
	cursor:pointer;
	position:relative;
	z-index:2001;
	line-height:20px;
	font-size:15px;
	//@extend .u-floatRight;
}

.globalHeader-menuTrigger:hover,
.globalHeader.isSticky .globalHeader-menuTrigger:hover {
	color:$brand;
}

.globalHeader-menuIcon {
	font-size: 17px;
	float:left;
	margin:0 5px 0 0;
	top: 2px;
    position: relative;
	@extend .u-inlineBlock;
}

@media (min-width:$screen-sm-min) {
	
}

@media (min-width:$screen-md-min) {
	.globalHeader {
		padding-top:10px;
		padding-bottom:10px;
	}
	.globalHeader-menuIcon {
		font-size:24px;
		margin:0;
		top:auto;
		float:none;
	}
	.globalHeader-menuTrigger {
		color:white;
		font-size:14px;
		padding:0;
		float:right;
	}
	.globalHeader-logo {
		height: 70px;
		width: 58px;
		top:auto;
	}
	.globalHeader.isSticky {
		background:white;
	}
	.globalHeader {
		background:transparent;
	}
	.globalHeader-logo {
		margin:0 2px 0 0;
		display:inline-block;
		vertical-align: middle;
	}
	.globalHeader-navContainer {
		padding:15px 0 0;
		text-align:left;
		float:right;
	}
	.globalHeader-menuIcon {
		margin:0 0 0 5px;
	}
	.globalHeader-navContainer {
		width: 575px;
	}
	.globalHeader-wrapper {
		display:block;
	}
	.globalHeader-tagline {
		display:inline;
		color:white;
		margin: 0;
		font-size: 18px;
		transition:color .35s;
	}
	.globalHeader.isSticky .navItem-link,
	.globalHeader.isSticky .globalHeader-menuTrigger {
		color:black;
	}

	.globalHeader.isSticky .navItem-icon {
		color:$brand;
	}

	.globalHeader.isSticky .navItem-btn {
		border-color:$brand;
		color:$brand;
	}

	.globalHeader.isSticky .globalHeader-tagline {
		color:black;
	}

	.globalHeader.isSticky .globalHeader-logo {
		height:42px;
		width:35px;
	}

	.globalHeader.isSticky .globalHeader-navContainer {
		padding-top:9px;
	}
	.globalHeader:after {
		display:none;
	}
	.globalHeader.isSticky:after {
		content: " ";
		width: 103%;
		height: 2px;
		background: rgba(0,0,0,.05);
		left: 0;
		position: absolute;
		bottom: -2px;
		display:block;
	}
}